<template>
  <div class="energyTrendAnalysis">
    <div class="left border-box tree">
      <el-tree
        :default-expand-all="true"
        :expand-on-click-node="false"
        :data="data"
        :props="defaultProps"
        @node-click="nodeClick"
      ></el-tree>
    </div>
    <div class="right border-box">
      <div class="search chartSearch">
        <div>
          <el-date-picker
            v-model="date1"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            @change="consumptionQueryFun"
            :clearable="false"
          >
          </el-date-picker>
        </div>
        <div style="color: #00b4fe">至</div>
        <div>
          <el-date-picker
            v-model="date2"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            @change="consumptionQueryFun"
            :clearable="false"
          >
          </el-date-picker>
        </div>
        <div>
          <span>部门</span>
          <el-select v-model="departVal" placeholder="" @change="lookupEqmFun">
            <el-option
              v-for="item in departOption"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </div>
        <div>
          <span>设备名称</span>
          <el-select
            v-model="eqmVal"
            placeholder=""
            @change="consumptionQueryFun"
          >
            <el-option
              v-for="item in eqmOption"
              :key="item.eqmId"
              :label="item.eqmName"
              :value="item.eqmId"
            >
            </el-option>
          </el-select>
        </div>
        <div style="flex-grow: 1; justify-content: flex-end; margin-right: 0">
          <button class="searchBth" @click="showEntryDialog">产能录入</button>
        </div>
      </div>
      <div class="tabel">
        <el-table
          :data="tableData"
          style="width: 100%"
          height="250"
          @row-click="rowClick"
        >
          <el-table-column prop="Department1" label="部门" width="150">
          </el-table-column>
          <el-table-column prop="EqmId1" label="设备编号" width="170">
          </el-table-column>
          <el-table-column prop="EqmName1" label="设备名称"> </el-table-column>
          <el-table-column prop="DateStr1" label="日期"> </el-table-column>
          <el-table-column prop="TCEQty1" label="耗能标煤"> </el-table-column>
          <el-table-column prop="OutPutQty1" label="产量"> </el-table-column>
          <el-table-column prop="UnitId1" label="产量单位"> </el-table-column>
          <el-table-column prop="ProductName1" label="产品名称">
          </el-table-column>
          <el-table-column prop="UnitIdOutPutQty1" label="单位产量">
          </el-table-column>
          <el-table-column prop="ElecQty1" label="电"> </el-table-column>
        </el-table>
      </div>
      <div class="border-box chart" style="flex-grow: 1">
        <e-Chart :arry="arry"></e-Chart>
      </div>
    </div>
    <entryDialog ref="entry"></entryDialog>
  </div>
</template>
<script>
import eChart from "./components/unitConsumptionEchart.vue";
import entryDialog from "./components/unitConsumptionAnalysis_dialog.vue";
import { places, energyTrend } from "@/network/energyTrendAnalysis";
import {
  lookupDepartment,
  lookupEqm,
  consumptionQuery,
  consumptionLine,
} from "@/network/unitConsumptionAnalysis";
import { Row } from "element-ui";
export default {
  components: {
    eChart,
    entryDialog,
  },
  data() {
    return {
      data: [],
      defaultProps: {
        children: "children",
        label: "label",
      },
      date1: "",
      date2: "",
      departOption: [],
      departVal: "",
      eqmOption: [],
      eqmVal: "",
      tableData: [],
      arry: {},
    };
  },
  mounted() {
    this.date2 = this.date1 =
      this.$getDate().year +
      "-" +
      this.$getDate().month +
      "-" +
      this.$getDate().day;
    this.placesFun();
  },
  methods: {
    /**
     * 获取当前用户的地区企业层级信息
     * **/
    placesFun() {
      places()
        .then((res) => {
          let flag = false;
          res.content.forEach((item) => {
            let idFirst = item.id.substring(0, 1);
            if (idFirst == "A" || idFirst == "C") {
              item["isClick"] = true;
              if (!flag) {
                this.areaId = item.id;
                flag = true;
              }
            } else {
              item["isClick"] = false;
            }
          });
          this.data = this.$arrToTree(res.content);
          this.lookupDepartmentFun();
        })
        .catch((err) => {});
    },
    /**
     * 点击tree节点
     * **/
    nodeClick(item) {
      if (item.isClick) {
        this.areaId = item.id;
        this.lookupDepartmentFun();
      }
    },
    /**
     * 获取部门下拉
     * **/
    lookupDepartmentFun() {
      lookupDepartment(this.areaId)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.departOption = content;
            this.departVal = content[0];
            this.lookupEqmFun();
          }
        })
        .catch((err) => {});
    },
    /**
     * 获取设备下拉
     * **/
    lookupEqmFun() {
      //判断如果生产部门是空的 设备名称 查询表格 折线图 都置空！！！
      if (!this.departVal) {
        this.eqmOption = [];
        this.eqmVal = "";
        this.tableData = [];
        this.arry = {};
        return;
      }
      lookupEqm(this.departVal)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.eqmOption = content;
            this.eqmVal = content[0].eqmId;
            this.consumptionQueryFun();
          }
        })
        .catch((err) => {});
    },
    /**
     * 单耗查询
     * **/
    consumptionQueryFun() {
      consumptionQuery(
        this.date1,
        this.areaId,
        this.departVal,
        this.eqmVal,
        this.date2
      )
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.tableData = content;
            this.rowClick(content[0]);
          }
        })
        .catch((err) => {});
    },
    /**
     * 点击产能录入BUTTON
     * **/
    showEntryDialog() {
      this.$refs.entry.showDialogFun();
    },
    /**
     * 表格点击某一行
     * **/
    rowClick(row) {
      this.consumptionLineFun(row);
    },
    /**
     * 序列化
     * **/
    transformation(data) {
      let params = new URLSearchParams();
      //遍历json对象的每个key/value对,p为key
      for (var item in data) {
        params.append(item, data[item]);
      }
      return params;
    },
    /**
     * 单耗查询折线图
     * **/
    consumptionLineFun(row) {
      let data = {
        json: JSON.stringify(this.tableData),
        dateStr: row.DateStr1,
        eqmId: row.EqmId1,
        eqmName: row.EqmName1,
      };
      consumptionLine(this.transformation(data))
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.arry = content;
          }
        })
        .catch((err) => {});
    },
  },
};
</script>
<style scoped lang="less">
.energyTrendAnalysis {
  background-image: url(@/assets/BG2.png);
  background-size: 100% 100%;
  display: flex;
  padding: 0.2rem;
  box-sizing: border-box;
  background-color: rgba(0, 15, 39, 1);
  width: 100%;
}
.border-box {
  border: 1px solid rgba(34, 175, 237, 0.5);
  border-radius: 0.05rem;
  background: rgba(0, 55, 146, 0.2);
  display: flex;
  flex-direction: column;
}
.left {
  width: 25%;
}
.right {
  flex-grow: 1;
  margin-left: 0.2rem;
  display: flex;
  flex-direction: column;
  padding: 0.2rem 0.2rem;
}
.search {
  display: flex;
  > div {
    margin-right: 0.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: nowrap;
  }
  span {
    color: #00b4fe;
    margin-right: 0.2rem;
    white-space: nowrap;
  }
}
.tabel {
  margin: 0.2rem 0 0;
}
.chart {
  flex-grow: 1;
}
/deep/ .el-date-editor.el-input,
/deep/ .el-date-editor.el-input__inner {
  width: 150px;
}
/deep/ .el-input__inner {
  width: 150px;
  background-color: rgba(0, 55, 146, 0.2);
  border: 1px solid rgba(34, 175, 237, 0.5);
}
/**表格**/
/deep/ .el-table,
/deep/ .el-table__expanded-cell,
/deep/ .el-table tr {
  background-color: transparent;
  color: #fff;
}
/deep/ .el-table th.el-table__cell {
  background-color: rgba(0, 90, 228, 0.5);
  color: #fff;
}
/deep/ .el-table td.el-table__cell,
/deep/ .el-table th.el-table__cell.is-leaf {
  border-bottom: none;
  text-align: center;
}
/deep/ .el-table--border::after,
/deep/ .el-table--group::after,
/deep/ .el-table::before {
  background-color: unset;
}
/deep/
  .el-table--striped
  .el-table__body
  tr.el-table__row--striped
  td.el-table__cell {
  background: rgba(0, 70, 176, 0.5);
  background-color: rgba(0, 70, 176, 0.5) !important;
}
/deep/
  .el-table--striped
  .el-table--enable-row-hover
  .el-table__body
  tr:hover
  > td.el-table__cell {
  background: rgba(0, 70, 176, 0.5);
}
/deep/
  .el-table--enable-row-hover
  .el-table__body
  tr:hover
  > td.el-table__cell {
  background-color: unset;
}
.searchBth {
  background: #0066ff;
  color: #fff;
  border-radius: 0.05rem;
  padding: 0 0.2rem;
  border: none;
  cursor: pointer;
  height: 100%;
}
/**树形**/
.el-tree {
  background: #fff0;
  color: #fff;
  padding: 0.2rem;
  overflow: scroll;
}
.tree /deep/ .expanded.el-tree-node__expand-icon {
  color: #fff !important;
}
.tree /deep/ .el-tree-node:focus > .el-tree-node__content,
.tree /deep/ .el-tree-node__content:hover,
.el-upload-list__item:hover {
  background-color: rgba(0, 70, 176, 0.2) !important;
}
/deep/ .el-input__prefix {
  right: 5px;
  left: unset;
}
/deep/ .el-input__inner {
  color: #fff;
}
/deep/ .el-input__icon {
  color: #00b4fe;
  font-size: 18px;
}

.normalBk {
  background: rgba(0, 54, 129, 1);
  color: #ffffff;
  cursor: pointer;
}
.tabAll {
  display: flex;
  div:first-child {
    border-radius: 0.04rem 0 0 0.04rem;
  }
  div:last-child {
    border-radius: 0rem 0.04rem 0.04rem 0;
  }
  div {
    font-size: 0.17rem;
    padding: 0.1rem 0.3rem;
    border-radius: 0.04rem 0.04rem 0 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0;
    margin: 0 0.02rem;
    .normalBk();
  }
}
.isActive {
  background: #0056dc !important;
}
</style>