<template>
  <div>
    <el-dialog
      title=""
      :visible.sync="dialogFormVisible"
      :modal="false"
      top="19vh"
    >
      <el-form :model="form">
        <el-form-item label="园区" :label-width="formLabelWidth">
          <el-select v-model="form.areaVal" placeholder="">
            <el-option
              v-for="item in areaOption"
              :key="item.areaId"
              :label="item.areaName"
              :value="item.areaId"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="企业" :label-width="formLabelWidth">
          <el-select
            v-model="form.companyVal"
            placeholder=""
            @change="lookupDepartmentFun"
          >
            <el-option
              v-for="item in companyOption"
              :key="item.companyId"
              :label="item.companyName"
              :value="item.companyId"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="日期" :label-width="formLabelWidth">
          <el-date-picker
            v-model="form.date"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            :clearable="false"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="产量" :label-width="formLabelWidth">
          <el-input v-model="form.outPutQty" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="产品名称" :label-width="formLabelWidth">
          <el-input v-model="form.productName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="部门" :label-width="formLabelWidth">
          <el-select
            v-model="form.selectDepartment"
            placeholder=""
            @change="lookupEqmFun"
          >
            <el-option
              v-for="item in departOption"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="设备名称" :label-width="formLabelWidth">
          <el-select v-model="form.selectEqmName" placeholder="">
            <el-option
              v-for="item in eqmOption"
              :key="item.eqmId"
              :label="item.eqmName"
              :value="item.eqmId"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="产能单位" :label-width="formLabelWidth">
          <el-input v-model="form.unitId" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confirm()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {
  areaList,
  companyList,
  lookupDepartment,
  lookupEqm,
  capacityInputAdd,
  capacityInputUpdate,
} from "@/network/unitConsumptionAnalysis";
export default {
  data() {
    return {
      dialogFormVisible: false,
      form: {
        areaVal: "",
        companyVal: "",
        date: "",
        outPutQty: "",
        productName: "",
        selectDepartment: "",
        selectEqmName: "",
        unitId: "",
      },
      formLabelWidth: "90px",
      areaOption: [],
      departOption: [],
      companyOption: [],
      eqmOption: [],
    };
  },
  watch: {},
  mounted() {},
  methods: {
    showDialogFun(state, row) {
      this.state = state;
      this.rowInfo = row;
      this.dialogFormVisible = true;
      this.areaListFun();
      this.companyListFun();
      if (state == "edit") {
        this.form = {
          areaVal: row.areaId,
          companyVal: row.companyId,
          date: row.date,
          outPutQty: row.outPutQty,
          productName: row.productName,
          selectDepartment: row.department,
          selectEqmName: row.eqmId,
          unitId: row.unitId,
        };
      }
    },
    /**
     * 获取园区列表
     * **/
    areaListFun() {
      areaList()
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.areaOption = content;
          }
        })
        .catch((err) => {});
    },
    /**
     * 获取当前用户下企业列表
     * **/
    companyListFun() {
      companyList()
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.companyOption = content.companyList;
          }
        })
        .catch((err) => {});
    },
    /**
     * 获取部门下拉
     * **/
    lookupDepartmentFun() {
      lookupDepartment(this.form.companyVal)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.departOption = content;
          }
        })
        .catch((err) => {});
    },
    /**
     * 获取设备下拉
     * **/
    lookupEqmFun() {
      lookupEqm(this.form.selectDepartment)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.eqmOption = content;
          }
        })
        .catch((err) => {});
    },
    confirm() {
      if (this.state == "add") {
        this.capacityInputAddFun();
      } else {
        this.capacityInputUpdateFun();
      }
    },
    /**
     * 产能录入新增
     * **/
    capacityInputAddFun() {
      let eqmName = "";
      this.eqmOption.forEach((item) => {
        if (item.eqmId == this.form.productName) {
          eqmName = item.eqmName;
        }
      });
      capacityInputAdd(
        this.form.areaVal,
        this.form.companyVal,
        this.form.date,
        this.form.outPutQty,
        this.form.productName,
        this.form.selectDepartment,
        this.form.selectEqmName,
        eqmName,
        this.form.unitId
      )
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.$message({
              message: "新增成功",
              type: "success",
            });
            this.dialogFormVisible = false;
            this.form = {
              areaVal: "",
              companyVal: "",
              date: "",
              outPutQty: "",
              productName: "",
              selectDepartment: "",
              selectEqmName: "",
              unitId: "",
            };
            this.$bus.$emit("unitRefresh", true);
          } else {
            this.$message.error(errmsg);
          }
        })
        .catch((err) => {});
    },
    /**
     * 产能录入修改
     * **/
    capacityInputUpdateFun() {
      let eqmName = "";
      this.eqmOption.forEach((item) => {
        if (item.eqmId == this.form.productName) {
          eqmName = item.eqmName;
        }
      });
      capacityInputUpdate(
        this.form.areaVal,
        this.form.companyVal,
        this.form.date,
        this.form.outPutQty,
        this.form.productName,
        this.form.selectDepartment,
        this.form.selectEqmName,
        eqmName,
        this.form.unitId,
        this.rowInfo.recId
      )
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.$message({
              message: "修改成功",
              type: "success",
            });
            this.dialogFormVisible = false;

            this.form = {
              areaVal: "",
              companyVal: "",
              date: "",
              outPutQty: "",
              productName: "",
              selectDepartment: "",
              selectEqmName: "",
              unitId: "",
            };
            this.$bus.$emit("uniitRefresh", true);
          } else {
            this.$message.error(errmsg);
          }
        })
        .catch((err) => {});
    },
  },
};
</script>
