<template>
  <div class="Pechart">
    <div class="echart" ref="echart"></div>
  </div>
</template>
<script>
export default {
  props: {
    arry: {
      type: Object,
      default: () => [],
    },
  },
  data() {
    return {};
  },
  watch: {
    arry(val) {
      this.setEchart();
    },
  },
  mounted() {},
  methods: {
    setEchart() {
      let chart = this.$refs.echart;
      let myChart = this.$echarts.init(chart);
      let option = {
        // dataZoom: [
        //   {},
        //   {
        //     type: "inside",
        //   },
        // ],
        title: {
          text: "",
        },
        tooltip: {
          trigger: "axis",
        },
        legend: {
          data: this.arry.data3,
          type: "scroll",
          orient: "vertical",
          left: "1%",
          top:'2%',
          textStyle: {
            color: "#fff",
          },
        },
        grid: {
          left: "25%",
          right: "1%",
          bottom: "2%",
          top: "5%",
          containLabel: true,
        },
        toolbox: {
          feature: {
            saveAsImage: {
              show: false,
            },
          },
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: this.arry.data1,
          axisLabel: {
            show: true,
            textStyle: {
              color: "#ffffff",
            },
          },
          axisTick: {
            show: false,
          },
        },
        yAxis: {
          type: "value",
          axisLabel: {
            show: true,
            textStyle: {
              color: "#ffffff",
            },
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: ["rgba(255, 255, 255, 0.5)"],
              type: "dashed",
            },
          },
        },
        series: this.arry.data2,
      };
      myChart.setOption(option);
      window.addEventListener("resize", function () {
        myChart.resize();
      });
    },
  },
};
</script>
<style scoped lang="less">
.echart {
  flex-grow: 1;
}
.Pechart {
  height: 100%;
  display: flex;
  flex-direction: column;
}
</style>