<template>
  <div>
    <el-dialog
      title=""
      :close-on-click-modal="false"
      :visible.sync="dialogVisible"
      :modal="false"
      width="80%"
    >
      <div class="search">
        <div>
          <el-date-picker
            v-model="date1"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            @change="capacityInputFun"
            :clearable="false"
          >
          </el-date-picker>
        </div>
        <div style="color: #00b4fe">至</div>
        <div>
          <el-date-picker
            v-model="date2"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            @change="capacityInputFun"
            :clearable="false"
          >
          </el-date-picker>
        </div>
        <div>
          <span>企业</span>
          <el-select
            v-model="companyVal"
            placeholder=""
            @change="companyListFun"
          >
            <el-option
              v-for="item in companyOption"
              :key="item.companyId"
              :label="item.companyName"
              :value="item.companyId"
            >
            </el-option>
          </el-select>
        </div>
        <div>
          <span>部门</span>
          <el-select v-model="departVal">
            <el-option
              v-for="item in departOption"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </div>
      </div>
      <div
        style="width: 100%; height: 6rem; margin-top: 0.29rem"
        class="dialog_content"
        v-if="dialogVisible"
      >
        <el-button
          type="primary"
          icon="el-icon-plus"
          style="
            background-color: #0066ff;
            border: none;
            margin-bottom: 0.15rem;
          "
          @click="addInfo"
          >新增</el-button
        >
        <el-table :data="tableData" style="width: 100%" height="100%">
          <el-table-column prop="areaId" label="园区编号"> </el-table-column>
          <el-table-column prop="companyId" label="企业编号"> </el-table-column>
          <el-table-column prop="department" label="部门"> </el-table-column>
          <el-table-column prop="eqmId" label="设备编号"> </el-table-column>
          <el-table-column prop="eqmName" label="设备名称"> </el-table-column>
          <el-table-column prop="date" label="日期"> </el-table-column>
          <el-table-column prop="outPutQty" label="产量"> </el-table-column>
          <el-table-column prop="unitId" label="产能单位"> </el-table-column>
          <el-table-column prop="productName" label="产品名称">
          </el-table-column>
          <el-table-column label="操作" width="100">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="editInfo(scope.row)"
                >修改</el-button
              >
              <el-button
                type="text"
                size="small"
                @click="capacityInputDeleteFun(scope.row)"
                >删除</el-button
              >
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-dialog>
    <addDialog ref="add_dialog"></addDialog>
  </div>
</template>
<script>
import {
  capacityInput,
  companyList,
  lookupDepartment,
  capacityInputDelete,
} from "@/network/unitConsumptionAnalysis";
import addDialog from "./unitConsumptionAnalysis_dialogAdd.vue";
export default {
  components: {
    addDialog,
  },
  data() {
    return {
      dialogVisible: false,
      tableData: [],
      companyOption: [],
      companyVal: "",
      departOption: [],
      departVal: "",
      date1: "",
      date2: "",
    };
  },
  watch: {},
  created() {
    this.$bus.$on("uniitRefresh", (msg) => {
      this.companyListFun(); //获取企业列表数据
    });
  },
  mounted() {},
  methods: {
    showDialogFun() {
      this.dialogVisible = true;
      this.date2 = this.date1 =
        this.$getDate().year +
        "-" +
        this.$getDate().month +
        "-" +
        this.$getDate().day;

      this.companyListFun(); //获取企业列表数据
    },
    /**
     * 获取当前用户下企业列表
     * **/
    companyListFun() {
      companyList()
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.companyOption = content.companyList;
            this.companyVal = content.companyList[0].companyId;
            this.lookupDepartmentFun();
          }
        })
        .catch((err) => {});
    },
    /**
     * 获取部门下拉
     * **/
    lookupDepartmentFun() {
      lookupDepartment(this.companyVal)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.departOption = content;
            this.departVal = content[0];
            this.capacityInputFun();
          }
        })
        .catch((err) => {});
    },
    /**
     * 参数：从日期、企业编号、部门、到日期
     * **/
    capacityInputFun() {
      capacityInput(this.date1, this.companyVal, this.departVal, this.date2)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.tableData = content;
          }
        })
        .catch((err) => {});
    },
    /**
     * 新增
     * **/
    addInfo() {
      this.$refs.add_dialog.showDialogFun("add");
    },
    /**
     * 修改
     * **/
    editInfo(row) {
      this.$refs.add_dialog.showDialogFun("edit", row);
    },
    /**
     * 删除
     * **/
    capacityInputDeleteFun(row) {
      capacityInputDelete(row.recId)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.companyListFun(); //获取企业列表数据
            this.$message({
              message: "删除成功",
              type: "success",
            });
          }
        })
        .catch((err) => {});
    },
  },
};
</script>
<style scoped lang="less">
/deep/ .el-dialog {
  background: #000000de;
}
/deep/ .el-dialog__headerbtn .el-dialog__close {
  color: #fff;
}
.search {
  display: flex;
  > div {
    margin-right: 0.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: nowrap;
  }
  span {
    color: #00b4fe;
    margin-right: 0.2rem;
    white-space: nowrap;
  }
}
.dialog_content {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
/deep/ .el-form-item__label {
  color: #ffffff;
}
</style>