import { request } from "./request";
import Vue from 'vue'

export function lookupDepartment(id) {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyManageAction/lookupDepartment?id=" + id,
        method: 'POST'
    })
}
export function lookupEqm(selectDepartment) {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyManageAction/lookupEqm?selectDepartment=" + selectDepartment,
        method: 'POST'
    })
}
export function consumptionQuery(fromDate,id,selectDepartment,selectEqmId,toDate) {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyManageAction/consumptionQuery?fromDate=" + fromDate+'&id='+id+'&selectDepartment='+selectDepartment+'&selectEqmId='+selectEqmId+'&toDate='+toDate,
        method: 'POST'
    })
}
export function capacityInput(fromDate,id,selectDepartment,toDate) {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyManageAction/capacityInput?fromDate=" + fromDate+'&id='+id+'&selectDepartment='+selectDepartment+'&toDate='+toDate,
        method: 'POST'
    })
}
export function companyList() {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyManageAction/companyList?areaId=" + sessionStorage.getItem('companyId'),
        method: 'POST'
    })
}
export function areaList() {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyManageAction/areaList",
        method: 'POST'
    })
}
export function capacityInputAdd(areaId,companyId,date,outPutQty,productName,selectDepartment,selectEqmId,selectEqmName,unitId) {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyManageAction/capacityInputAdd?areaId="+areaId+'&companyId='+companyId+'&date='+date+'&outPutQty='+outPutQty+'&productName='+productName+'&selectDepartment='+selectDepartment+'&selectEqmId='+selectEqmId+'&selectEqmName='+selectEqmName+'&unitId='+unitId,
        method: 'POST'
    })
}
export function capacityInputUpdate(areaId,companyId,date,outPutQty,productName,selectDepartment,selectEqmId,selectEqmName,unitId,recId) {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyManageAction/capacityInputUpdate?areaId="+areaId+'&companyId='+companyId+'&date='+date+'&outPutQty='+outPutQty+'&productName='+productName+'&selectDepartment='+selectDepartment+'&selectEqmId='+selectEqmId+'&selectEqmName='+selectEqmName+'&unitId='+unitId+'&recId='+recId,
        method: 'POST'
    })
}
export function capacityInputDelete(recId) {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyManageAction/capacityInputDelete?recId="+recId,
        method: 'POST'
    })
}
export function consumptionLine(data) {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyManageAction/consumptionLine",
        data:data,
        method: 'POST'
    })
}